<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入运营信息" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                <div class="m_l_20 w_100">
                    <el-select v-model="isDisplay" @change="search">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="addItem" icon='plus'>新建内容</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>

        <p class="tips" style="font-size: 12px; margin-bottom: 10px; color: #9a9a9a;">*轮播图上架数量上限为 4 张</p>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="图片" width="80">
                        <template scope="scope">
                            <el-button type="text" @click="showPic(scope.$index, scope.row)">预览</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="operationInfo" label="运营信息">
                    </el-table-column>
                    <el-table-column align="center" label="状态" :formatter="formatStatus">
                    </el-table-column>
                    <el-table-column align="center" label="上/下架时间" width="150" :formatter="formatTime">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="250">
                        <template scope="scope">
                            <el-button type="info" size="small" @click="editItem(scope.$index, scope.row)" icon="edit">编辑</el-button>
                            <el-button type="warning" v-show="scope.row.isDisplay == 2" size="small" @click="changeDisplay(scope.$index, scope.row, 1)">上架</el-button>
                            <el-button type="warning" v-show="scope.row.isDisplay == 1" size="small" @click="changeDisplay(scope.$index, scope.row, 2)">下架</el-button>
                            <!-- <el-button size="small" @click="changePosition(scope.$index, scope.row)">上移</el-button> -->
                            <!-- <el-button size="small" @click="changePosition(scope.$index, scope.row)">下移</el-button> -->
                            <el-button size="small" type="danger" @click="deleteItem(scope.$index, scope.row)" icon="delete2">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>

        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="edit dialog-info" :close-on-click-modal="false" title="编辑" :visible.sync="editDialog" @close="resetFields('editForm', 'editPicFile')">
            <el-form :model="eForm" ref="editForm" :rules="rules">
                <el-form-item label="运营信息：" prop="operationInfo">
                    <span>{{eLavaNum + '/' +maxLength}}</span>
                    <el-input type="textarea" :maxlength="maxLength" :rows="2" placeholder="请输入信息内容" v-model="eForm.operationInfo">
                    </el-input>
                </el-form-item>

                <el-form-item label="超链接：" prop="link">
                    <el-input type="text" v-model="eForm.link" placeholder="请输入轮播图跳转的超链接"></el-input>
                </el-form-item>

                <!-- <el-form-item class="upload-box" label="封面图：">
                    <img width="100%" :src="this.eForm.operationPicture" class="avatar">
                </el-form-item> -->

                <el-form-item class="upload-box" label="封面图：" prop="pic">
                    <el-upload
                      class="avatar-uploader"
                      :action="API + 'media/file_upload'"
                      ref="editPicFile"
                      :show-file-list="false"
                      :on-success="onSuccess">
                      <img v-if="picUrlList" :src="picUrlList" class="avatar">
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <p class="tips" style="font-size: 12px;color: #9a9a9a;margin-left: 80px">*轮播图上传规格：750*350</p>
                </el-form-item>

                <div class="info-radio">
                    <el-form-item>
                        <el-radio-group v-model="eForm.isDisplay">
                            <el-radio class="radio" :label="1">上架</el-radio>
                            <el-radio class="radio" :label="2">下架</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </div>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="editSave('editForm')">确 定</el-button>
            </div>
        </el-dialog>
    
        <el-dialog class="add dialog-info" title="新建" :close-on-click-modal="false" :visible.sync="addDialog" @close="resetFields('addForm', 'addPicFile')">
            <el-form :model="aForm" ref="addForm" :rules="rules">
                <el-form-item label="运营信息：" prop="operationInfo">
                    <span>{{aLavaNum + '/' +maxLength}}</span>
                    <el-input type="textarea" :maxlength="maxLength" :rows="2" placeholder="请输入信息内容" v-model="aForm.operationInfo">
                    </el-input>
                </el-form-item>

                <el-form-item label="超链接：" prop="link">
                    <el-input type="text" v-model="aForm.link" placeholder="请输入轮播图跳转的超链接"></el-input>
                </el-form-item>

                <el-form-item class="upload-box" label="封面图：" prop="pic">
                    <el-upload
                      class="avatar-uploader"
                      :action="API + 'media/file_upload'"
                      ref="addPicFile"
                      :show-file-list="false"
                      :on-success="onSuccess">
                      <img v-if="picUrlList" :src="picUrlList" class="avatar">
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <p class="tips" style="font-size: 12px;color: #9a9a9a;margin-left: 80px">*轮播图上传规格：750*350</p>
                </el-form-item>
                    
                <div class="info-radio">
                    <el-form-item>
                        <el-radio-group v-model="aForm.isDisplay">
                            <el-radio class="radio" :label="1">上架</el-radio>
                            <el-radio class="radio" :label="2">下架</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </div>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addDialog = false">取 消</el-button>
                <el-button type="primary" @click="addSave('addForm')">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync="viewPicDialog" class="view-pic" @close='closeViewPic'>
            <img width="100%" :src="picUrl">
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        const checkPic = (rule, value, callback) => {
            if (!this.picUrlList) {
              callback(new Error('请上传图片后重试'))
            } else {
              callback()
            }
        }
        return {
            maxLength: 100, //最大长度
            // 搜索
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            isDisplay: '',
            options: [{
                  value: '',
                  label: '全部'
                }, {
                  value: 1,
                  label: '上架'
                }, {
                  value: 2,
                  label: '下架'
            }],
            editDialog: false, //弹出框-编辑
            addDialog: false, //弹出框-新建
            // 编辑-表单
            eForm: {operationInfo: ''},
            // 添加-表单
            aForm: {operationInfo: ''},

            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            tableTop: 0,

            // 预览图
            viewPicDialog: false,
            // 上传预览
            dialogVisible: false,
            // 预览文件链接
            picUrl: null,
            // 上传文件链接
            picUrlList: null,

            // 验证规则
            rules: {
                pic: [{ validator: checkPic, trigger: 'change' }],
                operationInfo: [{required: true, message: '内容不能为空', trigger: 'blur'}],
                link: [{required: true, message: '链接不能为空', trigger: 'blur'}]
            }
        }
    },
    computed: {
        eLavaNum() {
            return this.eForm.operationInfo && this.eForm.operationInfo.length || 0
        },
        aLavaNum() {
            return this.aForm.operationInfo && this.aForm.operationInfo.length || 0
        },
        tableMaxHeight() {
            return (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0) - this.tableTop - 100
        }
    },
    props: ['API'],
    methods: {
        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word)
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            // 格式化状态
            formatStatus(row, column) {
                return row.isDisplay == 1 ? '上架' : row.isDisplay == 2 ? '下架' : '未知'
            },
        // // 查看-编辑
        editItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "id": row.id
                }
            }
            this.$common._post('homePage/detail', json, this, data => {
                this.eForm = Object.assign({}, data.banner)
                this.editDialog = true
                this.picUrlList = this.eForm.operationPicture
            })
        },
        // // 编辑保存
        editSave(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认修改?', '提示', {})
                        .then(() => {
                            this.eForm.picUrl = this.picUrlList
                            var json = {
                                "head": {
                                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                                },
                                "body": {
                                    "operation_info": this.eForm.operationInfo,
                                    "is_display": this.eForm.isDisplay,
                                    "id": this.eForm.id,
                                    "operation_picture": this.eForm.picUrl, //封面图
                                    "link": this.eForm.link,
                                }
                            }
                            this.$common._post('homePage/edit', json, this, data => {
                                this.editDialog = false
                                this.$message({
                                    type: 'success',
                                    message: '编辑成功!'
                                })
                                this.getList()
                            })
                        }).catch(()=>{})
                }
            })
        },
        // 查看-添加
        addItem() {
            this.aForm = {
                isDisplay: 1,
                operationInfo: '',
                picUrl: '',
                link: '',
            }
            this.picUrlList = ''
            this.addDialog = true
        },
        // 添加-保存
        addSave(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认添加?', '提示', {})
                        .then(() => {
                            this.aForm.picUrl = this.picUrlList
                            var json = {
                                "head": {
                                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                                },
                                "body": {
                                    "operation_info": this.aForm.operationInfo,
                                    "is_display": this.aForm.isDisplay,
                                    "operation_picture": this.aForm.picUrl,
                                    "link": this.aForm.link,
                                }
                            }
                            this.$common._post('homePage/add', json, this, data => {
                                this.addDialog = false
                                this.$message({
                                    type: 'success',
                                    message: '添加成功!'
                                })
                                this.getList()
                            })
                        }).catch(()=>{})
                }
            })
        },
        //更改上架/下架
        changeDisplay(index, row, status) {
            this.$confirm('是否确定修改?', '提示', {})
                .then(() => {
                    var json = {
                        "head": {
                            "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                            "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                        },
                        "body": {
                           "is_display": status,
                           "id": row.id
                        }
                    }
                    this.$common._post('homePage/editceil', json, this, data => {
                        this.$message({ type: 'success', message: '修改成功!' })
                        this.getList()
                    })
                }).catch(() => {})
        },
        //上移/下移位置
        changePosition(index, row) {
            // alert('暂无')
        },
        // 删除
        deleteItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "id": row.id
                }
            }
            this.$common.deleteItem(this.API + 'homePage/delete', this, json)
        },
        // 重置表单
        resetFields(formName, picFormName){
            this.$refs[formName].resetFields()
            picFormName && this.$refs[picFormName].clearFiles()
            this.eForm = {}
            this.picUrlList = null
        },
        // 上传成功
        onSuccess(res, file, fileList) {
            this.picUrlList = res.body.attachment_url
            this.$message('上传成功!')
        },
        // 预览图片
        showPic(index, row) {
            this.viewPicDialog = true
            this.picUrl = row.operationPicture
        },
        // 关闭时清空图片
        closeViewPic() {
            this.picUrl = ''
        },
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelHomePageBanner', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val
            this.getList(this.word)
        },
        // 获取列表
        getList(operationInfo = ''){
            this.listLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body":{
                    "pageIndex": this.currentPage,
                    "pageSize": this.pageSize,
                    "operation_info": operationInfo,
                    "is_display": this.isDisplay,
                    "orderBy": this.orderBy,
                }
            }
            this.$common._post('homePage/list', json, this, data => {
                this.total = data.total
                this.tableData = data.homePageBannerlist
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style lang="scss">
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #20a0ff;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
    }
    .avatar {
      width: 178px;
      height: 178px;
      display: block;
    }
    .view-pic {
        .el-dialog {
            // background: transparent;
            // border: none;
            // box-shadow: none;
        }
    }
</style>



